export async function setlimit(code,token) {
    const response = await fetch(
        'https://open.ihccs.com/test/company/admin/product/order/pay/voucher/review',
        {
            method: 'POST',
            headers: {
                'Authorization':`${token}`,
            },
            body: `payId=${code}&status=6`
        }
    )
    return response
}